import Vue from 'vue';
import Vuex from 'vuex';
import VuexPersistence from 'vuex-persist';
Vue.use(Vuex);
const state = {
  appCenter: {
    myApp: []
  }
}
const mutations = {
  setAppCenter(state, info) {
    // state.appCenter.myApp = info.myApp == undefined ? state.appCenter.myApp : info.myApp;
    state.appCenter.myApp = info.myApp || state.appCenter.myApp;
  }
}
const actions = {
  SetAppCenter({ commit }, info) {
    commit('setAppCenter', info);
  }
}
const getters = {
  getAppCenter(state) {
    return state.appCenter;
  }
}
const vuexLocal = new VuexPersistence({
  storage: window.localStorage,
  reducer: (state) => ({
    appCenter: state.appCenter
  }),
  filter: (mutation) => (
    mutation.type === 'setAppCenter'
  )
});
export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters,
  plugins: [vuexLocal.plugin]
});


