import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const routes = [{
  path: '/homePage',
  name: 'homePage',
  component: resolve => require(['@/web/homePage/homePage'], resolve)
}, {
  path: '/scroll',
  name: 'scroll',
  component: resolve => require(['@/web/scroll/scroll'], resolve)
}, {
  path: '/',
  name: 'workCenter',
  component: resolve => require(['@/web/workCenter/workCenter'], resolve)
}, {
  path: '/appCenter',
  name: 'appCenter',
  component: resolve => require(['@/web/appCenter/appCenter'], resolve)
}, {
  path: '/handle',
  name: 'handle',
  component: resolve => require(['@/web/handle/handle'], resolve)
}]

const router = new Router({
  mode: 'history',
  routes
});
export default router;



